const { useTranslation } = require('react-i18next');

export const useTranslateCategory = () => {
  const { t } = useTranslation();

  const translateCategory = item => {
    switch (item) {
      case '':
        return t('eggs');
      case '':
        return t('crops');
      case '':
        return t('flour');
      case '':
        return t('dairy');
      case '':
        return t('fish');
      case '':
        return t('meat');
      case '':
        return t('berries');
      case '':
        return t('sausage');
      case '  ':
        return t('oils');
      case '':
        return t('fruit');
      case '':
        return t('mushrooms');
      case '  ':
        return t('vegetables');
      case '':
        return t('nuts');
      case '':
        return t('seeds');
      case '':
        return t('driedFruits');
      case ' ':
        return t('alcoholic');
      case ' ':
        return t('nonAlcoholic');
      case '':
        return t('poppy');
      case '':
        return t('sesame');
      default:
        break;
    }
  };

  return { translateCategory };
};
